body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

#root2 {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: -webkit-gradient(linear, left top, right bottom, from(#ac5d94), to(#51fa2c));
  background: -webkit-linear-gradient(left top, #ac5d94, #51fa2c);
  background: -o-linear-gradient(left top, #ac5d94, #51fa2c);
  background: linear-gradient(to right bottom, #ac5d94, #51fa2c);
}



